
import {mapGetters} from 'vuex'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'


export default {
computed: mapGetters([]),
components: {
Toast,
XHeader,
Group,
Cell,
Divider,
XInput,
XButton,
Alert
},
data() {
return {
totalMoney: '',
totalNumber: '',
payCode: '',
showAlert: false,
errorShow: false,
successShow: false,
errorContent: '',
successContent: '',
maxLength: 6,
userInfo:'',
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
},
methods: {
confirm() {
if (isNaN(Number(this.totalMoney)) || isNaN(Number(this.totalNumber))) {
this.errorShow = true;
this.errorContent = '请输入数字';
return
}
if (!this.totalMoney || !this.totalNumber) {
this.errorShow = true;
this.errorContent = '请输入红包金额或数量';
return
}
this.showAlert = true;
},
onHide() {
if (this.payCode.length != 6) {
this.showAlert = true;
this.errorShow = true;
this.errorContent = '请输入完整的支付密码';
return
}
let addShare = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": this.userInfo.ui_ID,//操作员编码
"operateUserName": "",
"pcName": "",
"totalMoney": this.totalMoney,//推广金额
"totalTimes": this.totalNumber,//分享个数
"payCode": this.payCode,//支付密码
"userType":"0"
};
this.$store.dispatch('addHandRedEnvelopes', addShare)
.then(suc => {
this.successShow = true;
this.successContent = suc;
setTimeout(() => {
this.$router.push({name: 'RedEnvelopes'})
}, 1000)
}, err => {
this.errorShow = true;
this.errorContent = err;
})
},
},
}



