
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {XButton, Toast},
data() {
return {
loginNumber: '',
password: '',
errorShow: false,
errorContent: ''
}
},
methods: {

goRegister() {
this.$router.push({name: 'Register'})
},

loginSubmit() {
let Login = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"userID": this.loginNumber,
"password": this.password
};
this.$store.dispatch('loginSubmit',Login)
.then(suc=>{
sessionStorage.setItem('scoresumList',JSON.stringify(suc));
this.$router.push({name:'PersonalCenter'})
},err=>{
this.errorShow = true;
this.errorContent = err;
})
}
}
}



