
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
computed: mapGetters([
'userInfromationObj'
]),
components: {
XHeader,
Group,
Cell,
Loading,
Alert,
Toast
},
data() {
return {
userInfo: '',
showLoding: false,
showHead: false,
headImageUrl: '',
errorShow: false,
errorContent: '',
successShow: false,
successContent: '',
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.initData();
},
methods: {
initData() {
this.showLoding = true;
let selectUser = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"ui_ID": this.userInfo.ui_ID
};
this.$store.dispatch('initUserInfromation', selectUser)
.then(() => {
this.showLoding = false;
})
},
update() {
this.$router.push({name: 'UpdatePersonalData'})
},

replaceHeadImage() {
this.fileSelected()
this.showHead = true;
},
fileSelected() {
setTimeout(() => {
if (this.$refs.fileToUpload) {
this.$refs.fileToUpload.addEventListener('change', () => {
let file = this.$refs.fileToUpload.files[0];
var fd = new FormData();
fd.append("fileUpload", file);
var xhr = new XMLHttpRequest();
xhr.open("POST", "http://image.1000da.com.cn/PostImage/PostAppStore");
xhr.send(fd);
xhr.onreadystatechange = () => {
if (xhr.readyState == 4 && xhr.status == 200) {
let obj = JSON.parse(xhr.responseText);
this.headImageUrl = obj.data;
this.$store.commit('setHeadImage', obj.data)
}
}
})
}
}, 30)
},


uploadFile() {
var fd = new FormData();
fd.append("fileToUpload", document.getElementById('fileToUpload').files[0]);
var xhr = new XMLHttpRequest();
xhr.open("POST", "/PostImage/PostFile");
xhr.send(fd);
xhr.onreadystatechange = () => {
if (xhr.readyState == 4 && xhr.status == 200) {
document.getElementById("myDiv").innerHTML = xhr.responseText;
}
}
},

replaceImage() {

let updateUser = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"data": this.userInfromationObj
};
this.$store.dispatch('updatePersonalData', updateUser)
.then(suc => {
this.successShow = true;
this.successContent = suc;
this.initData();
}, err => {
this.errorShow = true;
this.errorContent = err;
})
}
},
}



