
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import Scroll from '../assets/public/HomeScroll.js'

export default {
computed: mapGetters([
'orderDataList'
]),
components: {
XHeader,
Actionsheet,
XButton,
Toast,
Loading
},
data() {
return {
menus: {
0: '旅行社',
1: '车票',
2: '门票',
3: '美食',
4: '酒店',
5: '租车',
'': '全部订单',
},
showMenus: false,
userInfo: '',
rows: 10,
id: '',
orderType: '全部订单',
errorShow: false,
errorContent: '',
successShow: false,
successContent: '',
showLoding: false,
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.initData();
},
mounted() {
Scroll(this)
},
methods: {
initData(id, rows) {
this.showLoding = true;
let getUserOrderDetail = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"huiuserid": this.userInfo.ui_ID,
"orderType": id ? id : '',
"page": "1",
"rows": rows ? rows : 10,
"isDelete":0
};
this.$store.dispatch('getOrderData', getUserOrderDetail)
.then(()=>{
this.showLoding = false;
})
},

changeOrderType(menuKey, menuValue) {
this.id = menuKey;
this.orderType = menuValue;
this.initData(this.id)
},

deleteOrder(id) {
let deleteOrder = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"orderID": id ? id : ''
};
this.$store.dispatch('deleteOrder',deleteOrder)
.then(suc => {
this.successShow = true;
this.successContent = suc;
this.initData();
}, err => {
this.errorShow = true;
this.errorContent = err;
})
}
},
}



