
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
computed: mapGetters([
'userInfromationObj',
'provinceDataList',
'cityDataList',
'occupationList',
'educationList'
]),
components: {
XHeader,
Group,
XInput,
Cell,
ButtonTab,
ButtonTabItem,
Datetime,
Selector,
XButton,
Toast
},
data() {
return {
sexItem: 0,
stateItem: 0,
endTiem: '',
proviceData: '',
cityData: '',
showProviceBox: false,
showCityBox: false,
showOccupationBox: false,
showEducationBox: false,
timeDate: '',
occupationData: '',
educationData: '',
errorShow: false,
errorContent: '',
successShow: false,
successContent: '',
}
},
created() {
let t = new Date();
let year = t.getFullYear();
let month = this.addZroe(t.getMonth() + 1);
let day = this.addZroe(t.getDate());
this.endTiem = year + "-" + month + "-" + day;
this.initProvinceList();
this.initOccupation();
this.initEducation();
},
methods: {

consoleIndex() {
},

stateIndex() {

},

addZroe(num) {
return num < 10 ? '0' + num : '' + num;
},

change(val) {
this.timeDate = val;
},

showProvice() {
this.showProviceBox = true;
},

closeShowProviceBox() {
this.showProviceBox = false;
},

showCity() {
this.showCityBox = true;
},

closeShowCity() {
this.showCityBox = false;
},

initProvinceList() {
let getAreaProvice = {
"areaPid": 3337
}
this.$store.dispatch('initProvinceData', getAreaProvice)
},

changeProvice(item) {
this.userInfromationObj.proviceName = item.sm_af_AreaName;
this.userInfromationObj.ui_Provice = item.sm_af_AreaID;
this.userInfromationObj.cityName = '';
this.userInfromationObj.ui_City = '';
this.showProviceBox = false;
this.initCityList(item.sm_af_AreaID)
},

changeCity(item) {
this.userInfromationObj.cityName = item.sm_af_AreaName;
this.userInfromationObj.ui_City = item.sm_af_AreaID;
this.cityData = item;
this.showCityBox = false;
},

initCityList(id) {
let getAreaProvice = {
"areaPid": id ? id : ''
}
this.$store.dispatch('initCityList', getAreaProvice)
},

initOccupation() {
let selectGroupJob = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"parentID": "38",
};
this.$store.dispatch('initOccupation', selectGroupJob)
},

showOccupation() {
this.showOccupationBox = true;
},

closeShowOccupationBox() {
this.showOccupationBox = false;
},

changeOccupation(item) {
this.userInfromationObj.ts_ui_JobID = item.value;
this.userInfromationObj.ts_ui_JobName = item.label
},

initEducation() {
let selectJob = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"ui_jb_ParentID": "39",
};
this.$store.dispatch('initEducation', selectJob)
},

showEducation() {
this.showEducationBox = true;
},

closeShowEducationBox() {
this.showEducationBox = false;
},

changeEducation(item) {
this.userInfromationObj.ts_ui_EducationID = item.ui_jb_ParentID;
this.userInfromationObj.ts_ui_EducationName = item.ts_jb_Name;
},

updateSubmit() {
let updateUser = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"data": this.userInfromationObj
};
this.$store.dispatch('updatePersonalData', updateUser)
.then(suc => {
this.successShow = true;
this.successContent = suc;
}, err => {
this.errorShow = true;
this.errorContent = err;
})
}
},
mounted() {

}
}



