
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import Scroll from '../assets/public/HomeScroll.js'

export default {
computed: mapGetters([
'exensionDetailsList'
]),
components: {
XHeader,
Loading
},
data() {
return {
rows: 20,
userInfo: '',
id: '',
showLoding: false
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.id = this.userInfo.ui_ID
this.initData(this.id);
},
methods: {
initData(id, rows) {
this.showLoding = true;
let getShareInfo = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",//操作员编码
"operateUserName": "",
"pcName": "",
"sm_ii_UserBeID": id ? id : '',//推广员编号
"page": "1",
"rows": rows ? rows : 10
};
this.$store.dispatch('initExensionDetails', getShareInfo)
.then(()=>{
this.showLoding = false;
},err=>{

})
},
},
mounted() {
Scroll(this)
},

}



