
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Alert from 'vux/src/components/alert/index.vue'


export default {
computed: mapGetters([]),
components: {
XHeader,
Group,
Cell,
Toast,
XButton,
Qrcode,
Alert
},
data() {
return {
errorShow: false,
successShow: false,
errorContent: '',
successContent: '',
linkAddress: '',
userInfo: '',
showqrCode: false,
linkAgain: '',
showqrAgainCode: false,

}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.linkAddress = this.userInfo.userExtendCode;
this.linkAgain = this.userInfo.proxyExtendCode;
},
methods: {

copy() {
var clipboard = new ClipboardJS('.btn');
clipboard.on('success', function (e) {
e.clearSelection();
});
this.successShow = true;
this.successContent = '复制成功！！'
},
showCode() {
this.showqrCode = true;
},
goExtensionDetails() {
this.$router.push({name: 'ExtensionDetails'})
},
showACode(){
this.showqrAgainCode = true;
}
},

}



