
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {XButton, Toast},
data() {
return {
getVerificationCode: '获取动态验证码',
codeDisabled: false,
telephoneNumber: '',
password: '',
verificationCode: '',
successShow: false,
errorShow: false,
successContent: '',
errorContent: '',
extensionID: '',
showExtensionID: false,
}
},
methods: {

goLogin() {
this.$router.push({name: 'Login'})
},

getCode() {
if (this.telephoneNumber == '') {
this.errorContent = '请输入电话号码';
this.errorShow = true;
return
}
let SendMessage = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"phone": this.telephoneNumber
}
this.$store.dispatch('getCode', SendMessage)
.then(suc => {
this.successContent = suc;
this.successShow = true;
let num = 59;
let timer = setInterval(() => {
if (num == 0) {
this.codeDisabled = false;
this.getVerificationCode = '重新获取验证码'
clearInterval(timer);
} else {
this.codeDisabled = true;
this.getVerificationCode = num + 's后重新获取';
num--;
}
}, 1000);
}, err => {
this.errorContent = err;
this.errorShow = true;
})
},

registerSubmit() {
console.log(this.extensionID)
return
if (this.telephoneNumber == '') {
this.errorContent = '请输入电话号码！';
this.errorShow = true;
return;
}

if (this.password == '') {
this.errorContent = '请输入密码！';
this.errorShow = true;
return;
}

if (this.verificationCode == '') {
this.errorContent = '请输入验证码！';
this.errorShow = true;
return;
}
let RegByCode = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"phone": this.telephoneNumber,
"extensionID": this.extensionID ? this.extensionID : '',
"password": this.password,
"validateNo": this.verificationCode
};
this.$store.dispatch('registerSubmit', RegByCode)
.then(suc => {
this.successContent = suc;
this.successShow = true;
this.login();
}, err => {
this.errorContent = err;
this.errorShow = true;
})
},

login() {
let Login = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"userID": this.telephoneNumber,
"password": this.password
};
this.$store.dispatch('loginSubmit', Login)
.then(suc => {
sessionStorage.setItem('scoresumList', JSON.stringify(suc));
this.$router.push({name: 'PersonalCenter'})

}, err => {
this.errorShow = true;
this.errorContent = err;
})
},

},
created() {
if (window.location.href.includes('?')) {
let id = window.location.href.split('?')[1].split('=')[1];
if (id.indexOf('#')) {
this.extensionID = id.slice(0, id.indexOf('#'));
} else {
this.extensionID = id;
}
}
;
if (this.extensionID) {
this.showExtensionID = false;
}else {
this.showExtensionID = true;
}
}
}



