
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
computed: mapGetters([]),
components: {
XHeader,
Group,
Cell,
Toast,
Loading
},
data() {
return {
userInfo: '',
moneyNum: 0,
errorShow: false,
successShow: false,
errorContent: '',
successContent: '',
showLoding: false,
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.searchMoney();
},
methods: {

searchMoney() {
this.showLoding = true;
let selectUserPot = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "huileyou",//操作员编码
"operatePassword": "123",//操作员密码
"operateUserName": "",          "pcName": "",
"sm_up_UserID": this.userInfo.ui_ID,//用户编码
};
this.$store.dispatch('searchMoney', selectUserPot)
.then(money => {
this.showLoding = false;
this.moneyNum = money;
}, err => {
this.errorShow = true;
this.errorContent = err;
})
},

goMoneyDetails(){
this.$router.push({name:'MoneyDetails'})
}
},
}



