
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import Scroll from '../assets/public/HomeScroll.js'

export default {
computed: mapGetters([
'moneyDetailsList'
]),
components: {
XHeader,
Toast,
Loading
},
data() {
return {
userInfo: '',
errorShow: false,
errorContent: '',
showLoding: false,
id: ''
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.searchMoneyId();
},
methods: {
searchMoneyId() {
this.showLoding = true;
let selectUserPot = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "huileyou",//操作员编码
"operatePassword": "123",//操作员密码
"operateUserName": "",        "pcName": "",
"sm_up_UserID": this.userInfo.ui_ID,//用户编码
"sm_up_PackageType": "0",//用户类型  0用户 1供应商
};
this.$store.dispatch('searchMoneyId', selectUserPot)
.then(id => {
this.id = id;
this.initData(this.id)
}, err => {
this.errorShow = true;
this.errorContent = err;
})
},
initData(id, rows) {
let getUserPotInfo = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "huileyou",//操作员编码
"operatePassword": "123",//操作员密码
"operateUserName": "",        "pcName": "",
"potID": id ? id : '',//钱包编码
"page": "1",
"rows": rows ? rows : 10
};
this.$store.dispatch('initMoneyDetails', getUserPotInfo)
.then(() => {
this.showLoding = false;
})
},
},
mounted() {
Scroll(this);
}
}



