
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
computed: mapGetters([
'searchUserIntegralList'
]),
components: {
XHeader,
Group,
Cell,
Toast,
Loading
},
data() {
return {
userInfo: '',
errorShow: false,
successShow: false,
errorContent: '',
successContent: '',
showLoding: false,
}
},
created() {
this.userInfo = JSON.parse(sessionStorage.getItem('scoresumList'));
this.searchUserIntegral();
},
methods: {

searchUserIntegral() {
this.showLoding = true;
let selectUserTotalScore = {
"loginUserID": "huileyou",//授权码
"loginUserPass": "123",//授权密码
"operateUserID": "",//操作员编码
"operateUserName": "",//操作员名称
"pcName": "",//机器码
"userCode": this.userInfo.ui_ID,//用户编码
};
this.$store.dispatch('searchUserIntegral', selectUserTotalScore)
.then(suc => {
this.showLoding = false;
}, err => {
this.errorShow = true;
this.errorContent = err;
})
},
},
}



